body {
  margin: 0;
  min-width: 320px;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  color: var(--parag-color);
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1870px;
}

.site-container {
  overflow: hidden;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  z-index: 3;
  padding: 37px 0;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  height: 31px;
}

.header__logo-svg {
  --bgc-woo: #9b5c8f;
  --words-woo: #fff;
  --txt-commerce: #000;
  width: 169px;
  height: 34px;
}

.header__nav {
  margin-left: -31.5%;
}

.header__search {
  transition: 0.2s;
}

.header__search-svg {
  --search-color: var(--title-color);
  width: 16px;
  height: 16px;
  transition: 0.2s;
}

.header__search:hover .header__search-svg {
  --search-color: var(--main-color);
}

.nav__item {
  display: inline-block;
}

.nav__item:not(:first-child) {
  margin-left: 38px;
}

.nav__link {
  font-weight: 500;
  line-height: 21px;
  color: var(--title-color);
  transition: 0.4s;
}

.nav__link:hover {
  color: var(--main-color);
}

.actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 254px;
}

.login {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: var(--title-color);
  transition: 0.4s;
}

.login:hover {
  color: var(--main-color);
}

.start {
  display: block;
  border: 2px solid transparent;
  border-radius: 5px;
  width: 120px;
  height: 47px;
  font-weight: 500;
  line-height: 45px;
  text-align: center;
  color: #fff;
  background-color: var(--main-color);
  transition: 0.4s;
}

.start:hover {
  border-color: var(--main-color);
  color: var(--main-color);
  background-color: transparent;
}

.burger {
  position: relative;
  z-index: 10;
  display: none;
  flex-direction: column;
  align-items: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: var(--main-color);
  cursor: pointer;
}

.burger__line {
  position: absolute;
  display: block;
  border-radius: 4px;
  padding: 0;
  width: 18px;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
}

.burger__line:nth-child(1) {
  top: 14px;
}

.burger__line:nth-child(2) {
  transform: rotate(90deg);
}

.burger__line:nth-child(2),
.burger__line:nth-child(3) {
  left: 19px;
  top: 19px;
  width: 1px;
}

.burger__line:nth-child(3) {
  right: 0;
}

.burger__line:nth-child(4) {
  top: 19px;
  bottom: 0;
}

.burger__line:nth-child(5) {
  bottom: 14px;
}

.burger_active .burger__line:nth-child(1) {
  top: 19px;
  background-color: transparent;
}

.burger_active .burger__line:nth-child(2) {
  left: 10.5px;
  width: 18px;
  transform: rotate(45deg);
}

.burger_active .burger__line:nth-child(3) {
  left: 10.5px;
  width: 18px;
  transform: rotate(-45deg);
}

.burger_active .burger__line:nth-child(4) {
  background-color: transparent;
}

.burger_active .burger__line:nth-child(5) {
  bottom: 19px;
  background-color: transparent;
}

.hero {
  position: relative;
  z-index: 2;
  padding: 270px 0;
  background: linear-gradient(180deg, #e5efff 0%, rgba(229, 239, 255, 0.26266) 83.7%, rgba(229, 239, 255, 0) 100%);
}

.hero__container {
  display: flex;
  justify-content: space-between;
}

.hero__txt {
  position: relative;
  z-index: 2;
  margin-left: 165px;
  max-width: 818px;
  line-height: 25px;
}

.hero__title {
  font-family: "CircularStd", sans-serif;
  font-weight: 700;
  font-size: 85px;
  line-height: 95px;
  color: var(--title-color);
}

.hero__parag {
  margin-top: 29px;
}

.hero__links {
  display: flex;
  align-items: center;
  margin-top: 31px;
}

.hero__img {
  position: absolute;
  right: 0;
  top: 180px;
  z-index: 2;
  width: 841px;
  height: 829px;
  background: url("../img/hero__img.png") center/cover no-repeat;
}

.custom__txt {
  margin-left: 12px;
}

.custom__link {
  margin-left: 12px;
  border-bottom: 1px solid transparent;
  color: var(--link-color);
  transition: 0.2s;
}

.custom__link:hover {
  border-color: var(--link-color);
}

.decore-w {
  position: absolute;
  right: -122px;
  top: 0;
  width: 1200px;
  height: 1100px;
}

.btn {
  display: inline-block;
  border: 2px solid transparent;
  border-radius: 60px;
  width: 267px;
  height: 72px;
  font-weight: 500;
  line-height: 71px;
  text-align: center;
  color: #fff;
  background-color: var(--main-color);
  transition: 0.4s;
}

.btn:hover {
  border-color: var(--main-color);
  color: var(--main-color);
  background-color: transparent;
}

.simple__section-head {
  max-width: 457px;
}

.simple__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 80px;
}

.simple__card:nth-child(2) {
  margin: 12px 0 0 -35px;
}

.simple__card:nth-child(3) {
  margin: 56px 0 0 -36px;
}

.section-head {
  position: relative;
  z-index: 2;
  margin: 0 auto;
  text-align: center;
}

.section-head__title {
  font-family: "CircularStd", sans-serif;
  font-weight: 700;
  font-size: 55px;
  line-height: 70px;
  text-align: center;
  color: var(--title-color);
}

.section-head__parag {
  margin-top: 20px;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.card__img {
  width: 536px;
}

.card__img_1 {
  height: 322px;
  background: url("../img/card__img_1.png") center/cover no-repeat;
}

.card__img_2 {
  height: 383px;
  background: url("../img/card__img_2.png") center/cover no-repeat;
}

.card__img_3 {
  height: 412px;
  background: url("../img/card__img_3.png") center/cover no-repeat;
}

.card__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 70px;
  color: var(--title-color);
}

.card__parag {
  margin-top: 2px;
  max-width: 430px;
  line-height: 30px;
}

.card__link {
  display: inline-block;
  margin-top: 28px;
  border-bottom: 1px solid transparent;
  font-weight: 700;
  color: var(--link-color);
  transition: 0.2s;
}

.card__link:hover {
  border-color: var(--link-color);
}

.support {
  position: relative;
  margin-top: 300px;
}

.support__container {
  position: relative;
}

.support__section-head {
  max-width: 660px;
}

.support__img {
  position: absolute;
  left: 0;
  right: 0;
  top: 147px;
  bottom: 0;
  z-index: -1;
  margin: 0 auto;
  width: 89%;
  height: 416px;
  background-image: url("../img/support__img.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.support__img_bg {
  top: 225px;
  z-index: -2;
  background-image: url("../img/support__img_bg.png");
}

.support__start {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 309px;
  padding: 78px 30px;
  background-color: #5f37ef;
}

.support__parag {
  max-width: 885px;
  font-size: 36px;
  line-height: 52px;
  color: #fff;
}

.support__btn {
  display: inline-block;
  border: 2px solid #fff;
  border-radius: 60px;
  width: 207px;
  height: 72px;
  font-weight: 700;
  font-size: 18px;
  line-height: 68px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  transition: 0.4s;
  cursor: pointer;
}

.support__btn:hover {
  color: #5f37ef;
  background-color: #fff;
}

.support__decore_ellipse {
  position: absolute;
  right: 130px;
  bottom: -24px;
  width: 89px;
  height: 89px;
  background: url("../img/support__decore_ellipse.png") center/cover no-repeat;
}

.footer__content {
  padding: 45px 0 64px;
  background-color: var(--main-color);
}

.footer__container {
  display: flex;
  flex-direction: column;
}

.footer__logo {
  --bgc-woo: #fff;
  --words-woo: var(--main-color);
  --txt-commerce: #fff;
  display: inline-block;
  margin: 135px auto 56px;
}

.footer__logo-svg {
  width: 183px;
  height: 37px;
}

.footer__line {
  margin: 0 auto;
  width: 82%;
  height: 1px;
  background-color: #9a7dff;
}

.footer__lists {
  display: flex;
  justify-content: center;
  margin-top: 40px;
  color: #fff;
}

.footer__list:not(:first-child) {
  margin-left: 130px;
}

.footer__item {
  font-size: 12px;
  line-height: 22px;
}

.footer__item:first-child {
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
}

.footer__link:hover {
  color: var(--title-color);
  transition: 0.2s;
}

.advantages {
  display: flex;
  justify-content: space-evenly;
  font-size: 20px;
  color: #fff;
}

.advantages__item {
  display: flex;
  align-items: center;
}

.advantages__svg {
  --footer-icon: #fff;
  width: 41px;
  height: 41px;
}

.advantages__parag {
  margin-left: 15px;
}

.copy {
  padding: 25px 0;
}

.copy__container {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.copy__img {
  width: 183px;
  height: 14px;
  background: url("../img/copy__img.png") center/cover no-repeat;
}

.social__link {
  transition: 0.4s;
}

.social__link:not(:first-child) {
  margin-left: 54px;
}

.social__link:hover .social__svg {
  --social-color: var(--main-color);
}

.social__svg {
  --social-color: var(--title-color);
  width: 20px;
  height: 20px;
  transition: 0.4s;
}

@media (max-width: 1830px) {
  .hero__txt {
    margin-left: 0;
    max-width: 629px;
  }
}

@media (max-width: 1600px) {
  .simple__cards {
    justify-content: space-evenly;
  }

  .card__img {
    width: 418px;
  }

  .card__img_1 {
    height: 234px;
  }

  .card__img_2 {
    height: 296px;
  }

  .card__img_3 {
    height: 322px;
  }

  .card__title {
    font-size: 28px;
  }

  .card__parag {
    max-width: 280px;
  }
}

@media (max-width: 1500px) {
  .header__nav {
    margin-left: 0;
  }
}

@media (max-width: 1335px) {
  .support__parag {
    max-width: 660px;
    font-size: 26px;
    line-height: 35px;
  }

  .support__img {
    top: 238px;
    width: 100%;
    height: 286px;
  }

  .support__img_bg {
    top: 300px;
  }

  .support__btn {
    width: 181px;
    height: 60px;
    line-height: 59px;
  }
}

@media (max-width: 1330px) {
  .hero__img {
    top: 255px;
    z-index: 2;
    width: 500px;
    height: 500px;
  }
}

@media (max-width: 1243px) {
  .card__img {
    width: 305px;
  }

  .card__img_1 {
    height: 171px;
  }

  .card__img_2 {
    height: 213px;
  }

  .card__img_3 {
    height: 235px;
  }
}

@media (max-width: 1100px) {
  .header__nav {
    display: none;
  }

  .header__actions {
    display: none;
  }

  .burger {
    display: flex;
  }
}

@media (max-width: 1000px) {
  body {
    font-size: 14px;
  }

  .hero__container {
    flex-direction: column;
  }

  .hero__txt {
    max-width: 100%;
    text-align: center;
  }

  .hero__title {
    font-size: 60px;
    line-height: 73px;
  }

  .hero__links {
    justify-content: center;
  }

  .hero__img {
    display: none;
  }

  .simple__card:nth-child(2) {
    margin: 0;
  }

  .simple__card:nth-child(3) {
    margin: 0;
  }

  .section-head__title {
    font-size: 40px;
    line-height: 50px;
  }

  .card {
    justify-content: space-between;
    max-width: 200px;
    text-align: center;
  }

  .card__img {
    margin: 0 auto;
    width: 200px;
    height: 150px;
  }

  .card__title {
    font-size: 20px;
    line-height: 28px;
  }

  .card__parag {
    margin-top: 12px;
    line-height: 23px;
  }

  .support__img {
    top: 136px;
    width: 320px;
    background-position: -437px;
  }

  .support__img_bg {
    top: 200px;
    width: 100%;
    background-position: center;
  }

  .support__start {
    flex-direction: column;
    margin-top: 200px;
    padding: 40px 30px;
